import React, { Component } from 'react';
import './App.css';
import 'react-bootstrap-accordion/dist/index.css'
import { Accordion } from 'react-bootstrap-accordion'
//test 
// 
class App extends Component {
  constructor (props) {
    super(props)
    this.state = {
      data:[],
      copy:'',
      id:-1
    }
  }
  componentDidMount(){
    this.setState({data:window.data.data,id:window.data.id})
  }
  show_data=(data,copy)=>{
    let rs=[];
    data.forEach((e,i) => {
      rs.push(
        <Accordion title={`${i+1}. ${e.title}`} key={i}>
          <div>
            {e.img_title.search("http")>-1&&<img src={e.img_title} style={{display:"block",margin:"auto"}}/>}
          </div>
          <div  className="textview">
            <div>
              <div>Bước 1: Nhập code liên hệ:</div>
              <div style={{position: 'relative'}}>
                <code  className="prettyprint prettyprinted">
                    {e.code}
                </code>
                <span className='tlv' onClick={()=>{
                    navigator.clipboard.writeText(e.code);
                    let {copy}=this.state;
                    copy+=i+',';
                    this.setState({copy:copy});
                    setTimeout(()=>{
                      this.setState({copy:""})
                    },1000)
                  }}>
                    {copy.search(i+',')>-1?"Đã sao chép":'Sao chép'}
                  </span>
              </div>
            </div>
              <div>Bước 2: Tải thư viện hình ảnh,icon: <a className='dlw'
                onClick={async()=>{
                  await localStorage.setItem("code",JSON.stringify({code:e.data_dowload.ma_code,id:this.state.id}));
                  window.open(e.data_dowload.url, "_blank");
                }}
              >Tải tại đây</a> sau đó upload lên website của bạn.</div>
              <div>
                <div>Bước 3: Điền các thông số theo yêu cầu như sau:</div>
                <div>
                  <ul>
                    {this.show_gia_tri(e.gia_tri)}
                  </ul>
                </div>
              </div>
              <div>
                <a href="#huong-dan">Xem hướng dẫn chi tiết</a>
              </div>
          </div>
        </Accordion>
      )
    });
    return rs;
  }
  //
  show_gia_tri=(gia_tri)=>{
    let rs=[];
    gia_tri.forEach((e,i) => {
      rs.push(<li key={i}>[<span style={{fontWeight:"600"}}>Giá trị {i}</span>]: {e}</li>)
    });
    return rs;
  }
  render() {
    let {data,copy}=this.state;
    return (
      <React.Fragment>
        {this.show_data(data,copy)}
      </React.Fragment>
    );
  }
}

export default App;
